import { TRANSFERT_AGENT_URL, TRANSFERT_CLIENT_URL } from ".";
import axios from "../http-common";

class TransferService {
  getListTransfers = async () => {
    const { data, error } = await axios.get(TRANSFERT_CLIENT_URL + "transfers");
    return { data, error };
  };

  createTransfer = async (transferInfo) => {
    const { data, error } = await axios.post(
      TRANSFERT_AGENT_URL + "transferAccountToAccount",
      {
        accountFrom: transferInfo["accountId"],
        accountTo: transferInfo["destinationAccountId"],
        amount: transferInfo["amount"],
        transferType: "ACCOUNT_TO_ACCOUNT",
        costType: transferInfo["soustraction"],
      }
    );
    console.log("data", data);
    return { data, error };
  };

  createTransferFromCahToAccount = async (transferInfo) => {
    const { data, error } = await axios.post(
      TRANSFERT_CLIENT_URL + "transferCashToAccount",
      {
        ... transferInfo,
        transferType: "CASH_TO_ACCOUNT",
      }
    );
    console.log("data", data);
    return { data, error };
  };
}

export default new TransferService();
